import React from 'react'
import ChoiceContainer from '../../ChoiceContainer'
import { useState } from 'react'
import { useEffect } from 'react'
import User from '../../User'

export default function Story1A(props) {
    const friend2 = User.getFriend(2)

    const [questionPrompt_1, setQuestionPrompt_1] = useState(null)
    const [mainChoiceContainer, setMainChoiceContainer] = useState(null)
    useEffect(() => {

        function setChoiceAreaButtons(event) {
            localStorage.setItem("1BChoice1", event.target.value)
            handleChoiceAreas(true)
        }
        function handleChoiceAreas(fromButton) {
            const choiceAreaDecision = localStorage.getItem("1BChoice1")
            switch (choiceAreaDecision) {
                case "A": 
                        if (fromButton === true) {
                            User.setStat("intoxication", +2)
                            User.setStat("playful", +1)
                            User.setAlignment("chaotic", +2)
                         }
                        setTimeout(()=>{setQuestionPrompt_1(<ChoiceAreaDialogueA />)}, 100)
                        toggleMainChoiceContainer()
                        break;
                case "B": 
                        if (fromButton === true) {
                            User.setAlignment("lawful", +2)
                            User.setAlignment("neutral", +1)
                        }
                        setTimeout(()=>{setQuestionPrompt_1(<ChoiceAreaDialogueB />)}, 100)
                        toggleMainChoiceContainer()
                        break;
                default: setQuestionPrompt_1(<ChoiceAreaQuestion1 />)
            }
        }

        function ChoiceAreaDialogueA() {
            return(
            <div className="choice-area-dialogue">
                <p>
                    You gladly extend your reach and claim your prize. You feel the cleansing properties of vodka at work.
                    A small fog creeps into your senses. This makes you really excited for its full effects.
                    Finished with your treat, you hand back the spirit canister. Richard lifts the flask in cheers, takes a swig, and tucks it away.
                </p>
                <p>
                    "Alright, that should help with the wait," Richard says through gritted teeth. He nods and starts towards the line.
                </p>
                <StoryDialog2 />
            </div>
            )} 
        function ChoiceAreaDialogueB() {
            return(
            <div className="choice-area-dialogue">
                <p>
                    "Nah, not right now," you say. You decide to maintain some grounding in the shakey nature of brunch preparation.
                </p>
                <p>
                    Richard looks at the flask in consideration, then nods and tucks it away for the future. "Yeah, good call. I shouldn't chance combining
                    a small buzz with hangry." Richard looks ahead, appearing ready for battle. "Alright, let's do this!" You and Richard march towards your place in line.
                </p>
                <StoryDialog2 />
            </div>
            )} 
        function StoryDialog2() {
            return(<>
                <p>
                    Finding the tail-end of the human chain proves slightly more difficult than you expected. There seems to be no clear system or organization.
                    The hungry crowd have all decided to each face different directions. The line seems to fork a few directions, as well, such as around a USPS Box or 
                    a chained up dog. 
                </p>
                <p>
                    You also notice a somewhat classist system at work: The dining-crowd laugh, drink, and feast with no hurry about them. They're like nobles enjoying
                    a grand meal surrounded by starving children; The ones finished leave with a smugness about them having been there and done that; Then there's you,
                    the peasants cast away by high society--at least for 30 minutes or so, anyhow.
                </p>
                <p>
                    You and Richard finally reach your newfound place in this world, letting out a small sigh. You immediately turn to scan those who sought brunch before you.
                    It doesn't take long for you to recognize a friend, who is typically known for their early-bird nature. 
                </p>
                
                {User.getStat("intoxication") > 0 ? 
                    <StoryDialog2A1 />
                    : <StoryDialog2A2 />
                    }
                <p>
                    "Looks like {friend2.pronoun}'s alone," Richard points out. "Should we get {friend2.pronounObj} to join us?"
                </p>
                </>
            )}
        function StoryDialog2A1() {
            return(<>
                <p>
                "{friend2.name}!!" you scream over the crowd. Perhaps it was the vodka starting its business, or maybe the venture of yelling it before Richard. 
                Either way, there isn't a market for it according to the looks of those around you.
                </p>
                <p>
                    {friend2.name} spins around in shock, looking somewhat embarassed. With eyes darting through the long group behind them, eventually {friend2.name} locks
                    eyes with you. <span style={{textTransform : "capitalize"}}>{friend2.posessive}</span> face changes to relief and a wave back is exchanged.
                </p>
                    </>
            )
        }
        function StoryDialog2A2() {
            return(<>
                "It's {friend2.name}." You wave eagerly but silent, trying not to disturb the already disturbed crowd. 
                Keeping things calm, sober, and collected is your mantra right now. Through mere chance or an innate instinct of being 
                watched, {friend2.name} slowly turns around and eventually sees your flailing arms. 
                </>
            )
        }

        function ChoiceAreaQuestion1() {
            return(
            <section className="choice-area-prompt">
                <button value="A" onClick={setChoiceAreaButtons}>Take a swig</button><button value="B" onClick={setChoiceAreaButtons}>Politely Decline</button>
            </section>)
        }

        function toggleMainChoiceContainer(){
            setTimeout(()=>{
                setMainChoiceContainer(
                    <ChoiceContainer 
                        content1={`Tell ${friend2.name} to join`}
                        content2='Cut the line'
                        value1='2A' value2='2B' 
                        userSelect={props.userSelect} 
                        choiceWindowClick={props.choiceWindowClick} />
            )}, 5000)
        }

        handleChoiceAreas()

    }, [props])

    return (
        <>
            <div className="game-story">
                <h2>Brunch Up Your Life</h2>
                <p>
                    "Let's do brunch." This decision seems so obvious that it was silly to even ponder other options.
            </p>
                <p>
                    "Good choice, {props.name}. Brunch sounds to die for," Richard responds with no hint of irony or foreshadowing, just simply saying
                the commonly used phrase.
            </p>
                <p>
                    To summarize your walk, it is decently productive--albiet, mostly uneventful: Sure, you help an elderly woman cross the street at some point. Yeah, you
                    fight the flames off of Richard's pants after he accidently walks through a burning bush (In his defense, he "thought he heard voices...").
                    Such dull affairs aren't worth expanding on when the real adventure,
                    like any and all adventures, begins only at-
            </p>
                <p>
                    "Ah, behold!"
            </p>
                <p>
                    Standing before you is a quaint, beige-brick building featuring long and colorful fencing; a roomy patio, forgetable neighboring residences, and a red-striped onning
                    with the words: <i>Brunch Up Your Life (Every Boy and Every Girl)</i>.
            </p>
                <p>
                    "Wow. It's... it's beautiful," you say in a breathy voice. Your appetite can't handle the welcoming aroma of fresh griddle treats, fresh orange juice,
                    and fresh gossip.
                </p>
                <p>
                    "Looks like we're about to do some waiting, {props.name}..."
                </p>
                <p>
                    Your mystified sights are disillusioned as you reevaluate the fencing--which, as it turns out, is actually a long and lively queue of afternoon famine.
                    You notice the average expression on each person is somewhere between 'God is good' and 'Why God, why'.
                </p>
                <p>
                    "Now how did I miss all that..." you wonder aloud.
                </p>
                <p>
                    "It's alright," Richard responds in a hopeful tone. "Let's hurry and get in line!" He gives you a suspicious... or maybe it's a playful look. "But first..."
                    Richard reaches into his pocket and gingerly lifts a shiny flask. Now smiling, he twists the cap off and offers you the first swig:
                </p>

                <hr></hr>
                {questionPrompt_1}

            </div>
            {mainChoiceContainer}
        </>
    )
}
