// import React from 'react'

const User = {
    name: 'userName',
    path: 'userPath',
    // Dev Key: 00-03 story setup, 1A-1B, 2A,2B,2C,2D, etc
    defaultUserPath: '00',

    friends: {
        1: {
            name: 'userFriend_1',
            pronoun: 'userFriendPronoun_1',
            pronounObj: 'userFriendPronounObj_1',
            posessive: 'userFriendPosessive_1'
        },
        2: {
            name: 'userFriend_2',
            pronoun: 'userFriendPronoun_2',
            pronounObj: 'userFriendPronounObj_2',
            posessive: 'userFriendPosessive_2'
        },
        3: {
            name: 'userFriend_3',
            pronoun: 'userFriendPronoun_3',
            pronounObj: 'userFriendPronounObj_3',
            posessive: 'userFriendPosessive_3'
        },
    },

    alignment: {
        chaotic: 'chaotic',
        neutral: 'neutral',
        lawful: 'lawful'
    },
    personality: {
        playful: 'playful',

    },
    stats: {
        intoxication: 'toxicity',
    },


    getName() {
        const userName = localStorage.getItem(User.name)
        return userName ? userName : "Friend"
    },
    setName(newUserName) {
        localStorage.setItem(User.name, newUserName)
    },

    getPath() {
        const userPath = localStorage.getItem(User.path)
        return userPath ? userPath : User.defaultUserPath
    },
    setPath(newUserPath) {
        localStorage.setItem(User.path, newUserPath)
    },

    getFriend(friendNumber) {
        const friendRef = User.friends[friendNumber]
        const friendName = localStorage.getItem(friendRef.name)
        const friendPronoun = localStorage.getItem(friendRef.pronoun)
        const friendPronounObj = localStorage.getItem(friendRef.pronounObj)
        const friendPosessive = localStorage.getItem(friendRef.posessive)

        const friendObj = {name: friendName, pronoun: friendPronoun, pronounObj: friendPronounObj, posessive: friendPosessive}
        return friendName ? friendObj : {name: "Friend"+friendNumber, pronoun: "he", pronounObj:"him", posessive: "his"}
    },
    setFriend(friendNumber, nameValue, gender) {
        const friendRef = User.friends[friendNumber]
        const friendName = 
            nameValue.charAt(0).toUpperCase() + nameValue.slice(1);
        localStorage.setItem(friendRef.name, friendName)


        switch(gender) {
            case "he": 
                localStorage.setItem(friendRef.pronoun, "he")
                localStorage.setItem(friendRef.pronounObj, "him")
                localStorage.setItem(friendRef.posessive, "his")
            break;
            case "she":
                localStorage.setItem(friendRef.pronoun, "she")
                localStorage.setItem(friendRef.pronounObj, "her")
                localStorage.setItem(friendRef.posessive, "her")
                break;
            case "they":
                localStorage.setItem(friendRef.pronoun, "they")
                localStorage.setItem(friendRef.pronounObj, "them")
                localStorage.setItem(friendRef.posessive, "their")
                break;
        }
    },

    getAlignment(alignment) {
        const userAlignment = localStorage.getItem(User.alignment[alignment])
        return userAlignment ? userAlignment : 0
    },
    setAlignment(alignment, value) {
        const alignmentRef = User.alignment[alignment]
        const str_userAlignment = localStorage.getItem(alignmentRef)
        const userAlignment = str_userAlignment ? parseInt(str_userAlignment) : 0
        localStorage.setItem(alignment, (userAlignment+value))
    },  
    
    getStat(stat) {
        const userStat = localStorage.getItem(User.stats[stat])
        return userStat
    },
    setStat(stat, value) {
        const statRef = User.stats[stat]
        const str_userStat = localStorage.getItem(statRef)
        const userStat = str_userStat ? parseInt(str_userStat) : 0
        localStorage.setItem(statRef, userStat + value)
    },

}

export default User