import React, {useRef, useEffect} from 'react'
import User from '../../User'
import './Friends.css'

export default function Friends() {
    // const [warning, setWarning] = useState('')

    useEffect(() => {
        function addContinueListener() {
            document.querySelector('.continue').addEventListener('click', handleContinueButton)
        }
        addContinueListener()
        return () => {
            document.querySelector('.continue').removeEventListener('click', handleContinueButton)
        };
    }, [])
    const nameRef1 = useRef()
    const nameRef2 = useRef()
    const nameRef3 = useRef()
    const genderRef1 = useRef()
    const genderRef2 = useRef()
    const genderRef3 = useRef()
    
    function handleContinueButton(event) {
        event.preventDefault()
        User.setFriend(1, nameRef1.current.value, genderRef1.current.value)
        User.setFriend(2, nameRef2.current.value, genderRef2.current.value)
        User.setFriend(3, nameRef3.current.value, genderRef3.current.value)
    }
    function handleInputCheck(event) {
        const x = event.target.value.toLowerCase()
        if(x.includes("dick") || x.includes("rich")) {
            event.target.parentNode.classList.add("warning")
            event.target.classList.add("warning")
            document.querySelector(".continue").classList.remove("show")
        } else {
            event.target.parentNode.classList.remove("warning")
            event.target.classList.remove("warning")
            
            if(nameRef1.current.value !== "" && nameRef2.current.value !== ""  && nameRef3.current.value !== "" && checkForRichardWarnings()) {
                document.querySelector(".continue").classList.add("show")
            } else {
                document.querySelector(".continue").classList.remove("show")

            }
        }
    }
    function checkForRichardWarnings() {
        const x = document.querySelectorAll(".warning")
        if(x.length > 0) {
            return false
        }
        else {
            return true
        }
    }
    const Select = React.forwardRef((props, ref) => (
            <select ref={ref}>
                <option value="he">"He"</option>
                <option value="she">"She"</option>
                <option value="they">"They"</option>
            </select>
    ))


    return (
        <>
            <h2 className="title">Three friends you'll see in your adventure:</h2>
            
            <form>
            <div>
                <input required ref={nameRef1} onChange={handleInputCheck} maxLength="12"></input>
                <Select ref={genderRef1}/>
            </div>
            <div>
                <input required ref={nameRef2} onChange={handleInputCheck} maxLength="12"></input>
                <Select ref={genderRef2}/>
            </div>
            <div>
                <input required ref={nameRef3} onChange={handleInputCheck} maxLength="12"></input>
                <Select ref={genderRef3}/>
            </div>
            <button type="button" value="03" className="choice-button continue">Continue</button>
            </form>
        </>
    )
}
